<script>
import axios from "axios";

import TextTitle from "@/components/control/TextTitle.vue";
import ShadowBox from "@/components/control/ShadowBox.vue";

export default {
  name: "ContactUsView",
  components: {
    TextTitle,
    ShadowBox,
  },
  data() {
    return {
      isLoading: "visibility:hidden;",
      allGamesList: {},
      problemTypeList: [
        this.$t("system.contactUs.problemAccount"),
        this.$t("system.contactUs.problemStoreConsume"),
        this.$t("system.contactUs.problemRunError"),
        this.$t("system.contactUs.problemEvent"),
        this.$t("system.contactUs.problemRole"),
        this.$t("system.contactUs.problemOther"),
      ],
      game: "",
      problemType: "",
      mail: "",
      roleId: "",
      problemDesc: "",
      // picUpload: []
    };
  },
  mounted() {
    let poxy = this;
    axios
      .post(
        "https://gaming-dev.murax2.com/api/getdata",
        {
          header: {
            language: "zh-Hant-TW",
          },
          requests: [
            {
              action: "listGames",
              data: {}, //all
            },
          ],
        },
        { headers: { "Content-Type": "application/json" } }
      )
      .then(function (res) {
        // console.log(res);
        let actionDatas = res.data.responses;
        poxy.allGamesList = {};
        for (let i = 0; i < actionDatas[0].data.length; i++) {
          poxy.allGamesList[actionDatas[0].data[i].id] =
            actionDatas[0].data[i].title;
        }
        poxy.isLoading = "visibility:visible;";
      })
      .catch(function (error) {
        console.log(error);
      });
  },
  methods: {
    // addFileChange(e) {
    //   var files = e.target.files || e.dataTransfer.files;
    // },
    confirm(event) {
      const forms = document.querySelectorAll(".needs-validation");
      if (!forms[0].checkValidity()) {
        event.preventDefault();
        event.stopPropagation();
      } else {
        let nextLine = "\r\n";
        let mailBody =
          "遊戲選擇:" +
          this.game +
          nextLine +
          "問題類型:" +
          this.problemType +
          nextLine +
          "電子信箱:" +
          this.mail +
          nextLine +
          "角色名稱/角色UID:" +
          this.roleId +
          nextLine +
          "問題描述:" +
          this.problemDesc;
        axios
          .post(
            "https://gaming-dev.murax2.com/api/sendFeedback",
            {
              token: "KSeok211OwqHbFvUNlw5InMDjY0VlYhZ",
              body: mailBody,
            }
          )
          .then(function (res) {
            console.log(res);
          })
          .catch(function (error) {
            console.log(error);
          });
        console.log(mailBody);
      }
      forms[0].classList.add("was-validated");
    },
  },
};
</script>

