<template>
  <div class="news_list_group"  :style="this.isLoading">
    <div class="container-fluid">
      <template v-for="newItem in datas" :key="newItem.id">
        <router-link :to="'/NewsDetail?id=' + newItem.id">
          <div class="row">
            <div class="col col-lg-4 col-12">
              <img
                :src="
                  newItem.game
                    ? newItem.game['thumbnail']
                    : getDefaultThumbnail()
                "
              />
              <h5 class="category">{{ "【" + newItem.category + "】" }}</h5>
            </div>
            <div class="col col-lg-5 col-md-6 col-12">
              <h5 class="title">{{ newItem.title }}</h5>
            </div>
            <div class="col col-lg-3 col-md-6 col-12">
              <h5 class="time">{{ getDate(newItem.posted_at) }}</h5>
              <i id="boot-icon" class="bi bi-caret-right-fill"></i>
            </div>
          </div>
        </router-link>
        <hr />
        <!-- v-if="index != datas.length - 1" -->
      </template>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      isLoading: "visibility:hidden;",
      datas: [
        {
          id: 5,
          category: "遊戲",
          title: "遊戲公告11/05",
          posted_at: 1667606400,
          game: {
            title: "遊戲2",
            thumbnail: "https://fakeimg.pl/400x400/D7BEB2/EEE/",
          },
        },
        {
          id: 14,
          category: "遊戲公告",
          title: "公告14號公告14號",
          posted_at: 1665450530,
          game: {
            title: "遊戲2",
            thumbnail: "https://fakeimg.pl/400x400/D7BEB2/EEE/",
          },
        },
        {
          id: 13,
          category: "遊戲‧遊戲",
          title: "公告13號公告13號公告13號",
          posted_at: 1665440530,
          game: {
            title: "遊戲2",
            thumbnail: "https://fakeimg.pl/400x400/D7BEB2/EEE/",
          },
        },
      ],
    };
  },
  mounted() {
    let poxy = this;
    axios
      .post(
        "https://gaming-dev.murax2.com/api/getdata",
        {
          header: {
            language: "zh-Hant-TW",
          },
          requests: [
            {
              action: "listNews",
              data: {
                begin: 1,
                length: 5,
              },
            },
          ],
        },
        { headers: { "Content-Type": "application/json" } }
      )
      .then(function (res) {
        let actionDatas = res.data.responses;
        poxy.datas = actionDatas[0].data;
        poxy.isLoading = "visibility:visible;";
      })
      .catch(function (error) {
        console.log(error);
      });
  },
  methods: {
    getDate: function (timestamp) {
      let date = new Date(timestamp * 1000);
      let dataValues = [
        date.getFullYear(),
        date.getMonth() + 1,
        date.getDate(),
        date.getHours(),
        date.getMinutes(),
        date.getSeconds(),
      ];
      if (dataValues[1] < 10) {
        dataValues[1] = "0" + dataValues[1];
      }
      if (dataValues[2] < 10) {
        dataValues[2] = "0" + dataValues[2];
      }
      if (dataValues[3] < 10) {
        dataValues[3] = "0" + dataValues[3];
      }
      if (dataValues[4] < 10) {
        dataValues[4] = "0" + dataValues[4];
      }
      // console.log(
      //   "Date: " +
      //     dataValues[0] +
      //     "/" +
      //     dataValues[1] +
      //     "/" +
      //     dataValues[2] +
      //     " " +
      //     dataValues[3] +
      //     ":" +
      //     dataValues[4] +
      //     ":" +
      //     dataValues[5]
      // );

      return (
        dataValues[1] +
        "/" +
        dataValues[2] +
        " " +
        dataValues[3] +
        ":" +
        dataValues[4]
      );
    },
    getDefaultThumbnail: function () {
      return require("@/assets/Loge_news.jpg");
    }, // default icon
  },
};
</script>

<style scoped lang="scss">
.news_list_group {
  margin: 15px 20px;
  margin-bottom: 40px;
  a {
    text-decoration: none;
    color: #727171;
  }
  hr {
    border-bottom: 1px solid #727171;
    margin: 0;
  }
  h5 {
    margin: unset;
    color: #727171;
    font-weight: unset;
  }
  img {
    height: 50px;
    width: 50px;
    border-radius: 50%;
  }
  .category {
    width: 150px;
    color: #727171;
    font-weight: bold;
    margin-left: 20px;
  }
  .title {
    padding: 10px 0;
  }
  .time {
    text-align: right;
    padding: 10px 0;
    ~ i {
      margin-left: 20px;
    }
  }
  .col {
    display: flex;
    align-items: center;
  }
  .col:has(.time) {
    justify-content: flex-end;
  }
  .row {
    padding: 1rem 0;
    border-radius: 15px;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
    &:hover {
      cursor: pointer;
      background-color: #d3d1d0;
    }
  }
}
</style>
