<template>
  <div class="choose_bar container">
    <div class="leftBtn" @click="preBtnFunc">
      <img src="@/assets/LeftBtn.png" />
    </div>
    <template v-for="(item, index) in datas" :key="item.id">
      <picture :class="'imgBox container pic_' + index">
        <!-- index == 1 ? 'imgBox container show' : 'imgBox container' -->
        <img :src="item.banner_url" />
      </picture>
    </template>
    <div class="rightBtn" @click="nextBtnFunc">
      <img src="@/assets/RightBtn.png" />
    </div>
    <div class="indicators">
      <div
        v-for="(item, index) in datas"
        :key="item.id"
        :class="(index == this.showIndex ? 'active' : '') + ' pic_' + index"
      ></div>
    </div>
  </div>
</template>

<script>
// import axios from "axios";
import $ from "jquery";
let SHOW = 1;
let CON_MARGIN = 0;

export default {
  props: {
    datas: {
      default: function () {
        var ary = [
          {
            id: 3,
            type: "game",
            banner_url:
              "https://mura-gaming-dev.s3.ap-northeast-1.amazonaws.com/3_1ce3e2df31.jpg",
            started_at: 1667433600,
            ended_at: 1668297600,
            game_id: 1,
          },
          {
            id: 2,
            type: "game",
            banner_url:
              "https://mura-gaming-dev.s3.ap-northeast-1.amazonaws.com/2_913fb40a08.jpg",
            started_at: 1667347200,
            ended_at: 1668211200,
            game_id: 1,
          },
          {
            id: 1,
            type: "game",
            banner_url:
              "https://mura-gaming-dev.s3.ap-northeast-1.amazonaws.com/1_0f504473ea.jpg",
            started_at: 1667260800,
            ended_at: 1668124800,
            game_id: 1,
          },
        ];
        return ary;
      },
      type: Object,
    },
  },
  data() {
    return {
      showIndex: 1,
    };
  },
  updated() {
    this.reset();
  },
  methods: {
    reset: function () {
      const timer = setInterval(() => {
        let $chooseBar = $(".choose_bar").css("visibility", "hidden");
        let a$imgs = $chooseBar.find(".imgBox");
        let $ShowImg = $($chooseBar.find("img")[this.showIndex]);
        if ($ShowImg[0].complete) {
          $chooseBar.height($ShowImg.height());
          CON_MARGIN =
            ($chooseBar.outerWidth(true) - $chooseBar.outerWidth()) / 2;
          CON_MARGIN = CON_MARGIN > 200 ? 200 : CON_MARGIN;
          let nConLeft = 0;

          for (let i = 0; i < a$imgs.length; i++) {
            let $pic = $(a$imgs[i]);
            if (i == this.showIndex - 1) {
              $pic.removeClass("centerImg");
              $pic.css("left", -CON_MARGIN + "px");
              $pic.css("transform-origin", "left");
            } else if (i == this.showIndex) {
              $pic.addClass("centerImg");
              $pic.css("left", nConLeft + "px");
            } else if (i == this.showIndex + 1) {
              $pic.removeClass("centerImg");
              $pic.css("right", -CON_MARGIN + "px");
              $pic.css("transform-origin", "right");
            }
          }
          clearInterval(timer);
          $chooseBar.css("visibility", "visible");
        }
      }, 10);
      // console.log($chooseBar);
    },
    preBtnFunc: function () {
      if (SHOW - 1 < 0) return;
      let nextIndex = SHOW - 1;
      let $chooseBar = $(".choose_bar");
      let nConLeft = 0;
      //indicators bar
      this.indicatorsFunc(nextIndex);
      //show item
      let $showImg = $chooseBar.find(".pic_" + nextIndex);
      $showImg.css("transform-origin", "");
      $showImg.css("z-index", "9");
      $showImg.animate(
        {
          left: nConLeft + "px",
          opacity: 1, //addClass("centerImg")
        },
        500
      );
      $showImg.addClass("centerImg");
      //hide item
      let $hideImg = $chooseBar.find(".pic_" + SHOW);
      $hideImg.css("transform-origin", "right");
      $hideImg.css("z-index", "1");
      $hideImg.animate(
        {
          left: "initial",
          right: -CON_MARGIN + "px",
          transform: "scale(0.9)", //removeClass("centerImg")
          opacity: 0.6, //removeClass("centerImg")
        },
        500
      );
      $hideImg.removeClass("centerImg");
      $hideImg.css("left", "initial");

      SHOW = nextIndex;
      console.log($chooseBar);
    },
    nextBtnFunc: function () {
      if (SHOW + 1 > this.datas.length - 1) return;
      let nextIndex = SHOW + 1;
      let $chooseBar = $(".choose_bar");
      let nConLeft = 0;
      //indicators bar
      this.indicatorsFunc(nextIndex);
      //show item
      let $showImg = $chooseBar.find(".pic_" + nextIndex);
      $showImg.css("transform-origin", "");
      $showImg.css("z-index", "9");
      $showImg.animate(
        {
          right: "",
          left: nConLeft + "px",
          opacity: 1, //addClass("centerImg")
        },
        400
      );
      $showImg.addClass("centerImg");
      //hide item
      let $hideImg = $chooseBar.find(".pic_" + SHOW);
      $hideImg.css("transform-origin", "left");
      $hideImg.css("z-index", "1");
      $hideImg.animate(
        {
          right: "initial",
          left: -CON_MARGIN + "px",
          transform: "scale(0.9)", //removeClass("centerImg")
          opacity: 0.6, //removeClass("centerImg")
        },
        400
      );
      $hideImg.removeClass("centerImg");
      $hideImg.css("right", "initial");

      SHOW = nextIndex;
      console.log($chooseBar);
    },
    indicatorsFunc: function (nShowIndex) {
      let $indicators = $(".choose_bar").find(".indicators");
      let aDivs = $indicators.find("div");
      for (let i = 0; i < aDivs.length; i++) {
        let $div = $(aDivs[i]);
        if (i == nShowIndex) {
          $div.animate(
            {
              width: "4em",
              "border-radius": "2em",
              "background-color": "#fa79fe",
            },
            300
          );
          $div.addClass("active");
        } else {
          $div.animate(
            {
              width: "1em",
              "border-radius": "50%",
              "background-color": "#ffffff",
            },
            300
          );
          $div.removeClass("active");
        }
      }
    },
  },
  created() {
    window.addEventListener("resize", this.reset);
  },
  unmounted() {
    window.removeEventListener("resize", this.reset);
  },
};
</script>

<style scoped lang="scss">
.choose_bar {
  width: 100%;
  position: relative;
  .centerImg {
    z-index: 9;
    > img {
      box-shadow: 0px 3px 5px 0px #6e6e6e;
    }
  }
  .imgBox {
    width: 100%;
    position: absolute;
    top: 0;
    img {
      cursor: pointer;
      -webkit-transition: all 0.5s;
      -moz-transition: all 0.5s;
      -o-transition: all 0.5s;
      transition: all 0.5s;
      min-width: 100%;
      &:hover {
      }
    }
    &:not(.centerImg) {
      transform: scale(0.9);
      opacity: 0.6;
    }
  }
  .leftBtn {
    position: absolute;
    left: 0;
    z-index: 99;
    height: 100%;
    display: flex;
    align-items: center;
    img {
      cursor: pointer;
      border-radius: 50%;
      -webkit-transition: all 0.5s;
      -moz-transition: all 0.5s;
      -o-transition: all 0.5s;
      transition: all 0.5s;
      opacity: 0;
    }
    &:hover img {
      box-shadow: 0px 0px 15px 0px #fa79fe;
    }
  }
  .rightBtn {
    position: absolute;
    z-index: 99;
    right: 0;
    height: 100%;
    display: flex;
    align-items: center;
    img {
      cursor: pointer;
      border-radius: 50%;
      -webkit-transition: all 0.5s;
      -moz-transition: all 0.5s;
      -o-transition: all 0.5s;
      transition: all 0.5s;
      opacity: 0;
    }
    &:hover img {
      box-shadow: 0px 0px 15px 0px #fa79fe;
    }
  }

  &:hover .leftBtn > img,
  &:hover .rightBtn > img {
    opacity: 0.8;
  }
  .indicators {
    position: absolute;
    z-index: 99;
    bottom: 0;
    left: 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0.5em 0;
    opacity: 0.8;
    div {
      width: 1em;
      height: 1em;
      border-radius: 50%;
      background-color: #ffffff;
      margin: 0 0.5em;
      &:is(.active) {
        width: 4em;
        border-radius: 2em;
        background-color: #fa79fe;
      }
    }
  }
}

@media (max-width: 1200px) {
}
</style>
