<template>
  <main :style="this.isLoading">
    <div class="container">
      <div class="row mainBox">
        <ShadowBox>
          <div class="spanDiv"></div>
          <div class="spanDiv"></div>
          <div class="titleDiv">
            <TextTitle :button="false">{{
              $t("system.featuredGames")
            }}</TextTitle>
            <div class="btnDiv">
              <div class="list">
                <img
                  :class="getShowType() == 'grid' ? 'show' : ''"
                  src="@/assets/ListBtn.svg"
                  height="35"
                  @click="clickEvent"
                />
                <img
                  :class="
                    getShowType() == 'list' ? 'show selected' : 'selected'
                  "
                  src="@/assets/ListBtn-selected.svg"
                  height="35"
                  @click="clickEvent"
                />
              </div>
              <div class="grid">
                <img
                  :class="getShowType() == 'list' ? 'show' : ''"
                  src="@/assets/GridBtn.svg"
                  height="35"
                  @click="clickEvent"
                />
                <img
                  :class="
                    getShowType() == 'grid' ? 'show selected' : 'selected'
                  "
                  src="@/assets/GridBtn-selected.svg"
                  height="35"
                  @click="clickEvent"
                />
              </div>
            </div>
          </div>
          <div class="spanDiv"></div>
          <div class="spanDiv"></div>
          <GameList
            :key="componentKey"
            :type="getShowType() == 'grid' ? 0 : 1"
            :direction="getShowType() == 'grid' ? 0 : 1"
            :datas="games"
            :detail="true"
            caption="left"
            :icon="true"
          ></GameList>
        </ShadowBox>
      </div>
      <div class="row">
        <PaginationCtrl></PaginationCtrl>
      </div>
    </div>
  </main>
</template>

<script>
import $ from "jquery";
import axios from "axios";

import TextTitle from "@/components/control/TextTitle.vue";
import ShadowBox from "@/components/control/ShadowBox.vue";
import GameList from "@/components/control/GameList.vue";
import PaginationCtrl from "@/components/control/PaginationCtrl.vue";

export default {
  name: "FeaturedGamesView",
  components: {
    TextTitle,
    ShadowBox,
    GameList,
    PaginationCtrl,
  },
  data() {
    return {
      isLoading: "visibility:hidden;",
      games: [],
      componentKey: 0, //for refresh
    };
  },
  mounted() {
    let poxy = this;
    axios
      .post(
        "https://gaming-dev.murax2.com/api/getdata",
        {
          header: {
            language: "zh-Hant-TW",
          },
          requests: [
            {
              action: "listGames",
              data: {
                length: 12,
                orderBy: "date",
                order: "desc",
                type: "featured",
              },
            },
          ],
        },
        { headers: { "Content-Type": "application/json" } }
      )
      .then(function (res) {
        let actionDatas = res.data.responses;
        poxy.games = actionDatas[0].data;
        poxy.isLoading = "visibility:visible;";
      })
      .catch(function (error) {
        console.log(error);
      });
  },
  methods: {
    clickEvent(event) {
      if (
        (event && !$(event.target).hasClass("selected")) ||
        !$(event.target).hasClass("show")
      ) {
        $(event.target).siblings().addClass("show");
        $(event.target).removeClass("show");
        var $another = $(event.target).parent().siblings().find(".show");
        $another.siblings().addClass("show");
        $another.removeClass("show");
        console.log($(event.target).parent().attr("class"));
        let nowShow = $(event.target).parent().attr("class");
        this.$cookies.set("gameShowType", nowShow);
        this.componentKey += 1; //for refresh
      }
    },
    getShowType() {
      return this.$cookies.get("gameShowType") || "grid";
    },
  },
};
</script>

