<template>
  <main :style="this.isLoading">
    <div class="container">
      <ShadowBox>
        <div class="row">
          <div class="col titleDiv">
            <TextTitle :button="false">{{ $t("system.news") }}</TextTitle>
          </div>
        </div>
        <div class="row news_title">
          <div class="col col-lg-4 col-12">
            <img :src="item.thumbnail" />
            <h5 class="category">{{ "【" + item.category + "】" }}</h5>
          </div>
          <div class="col col-lg-5 col-md-6 col-12">
            <h5 class="title">{{ item.title }}</h5>
          </div>
          <div class="col col-lg-3 col-md-6 col-12">
            <h5 class="time">{{ getDate(item.posted_at) }}</h5>
            <i id="boot-icon" class="bi bi-caret-right-fill"></i>
          </div>
          <hr />
          <div class="col col-12">
            <p>{{ item.content }}</p>
          </div>
        </div>
      </ShadowBox>
      <div class="row choose_bar">
        <div>
          <router-link to="/News">
            {{ $t("system.backNewsList") }}
          </router-link>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
import TextTitle from "@/components/control/TextTitle.vue";
import ShadowBox from "@/components/control/ShadowBox.vue";
import axios from "axios";

export default {
  name: "NewsDetailView",
  components: {
    TextTitle,
    ShadowBox,
  },
  data() {
    return {
      isLoading: "visibility:hidden;",
      item: {
        id: 15,
        category: "平台公告",
        title: "公告15號",
        thumbnail: "https://fakeimg.pl/400x400/D7BEB2/EEE/",
        content:
          "公告15號內文公告15號內文公告15號內文公告15號內文公告15號內文公告15號內文\r\n" +
          "公告15號內文公公告15號內文公公告15號內文公公告15號內文公告15號內文公告15號內文公告15號內文公告15號內文公告15號內文\r\n" +
          "公告15號內文公告15號內文公告公告15號內文公告15號內文\r\n" +
          "公告15號內文公告15號內文公告15號內文公告15號內文公文公告15號內文\r\n",
        posted_at: 1665455530,
      },
    };
  },
  mounted() {
    let poxy = this;
    axios
      .post(
        "https://gaming-dev.murax2.com/api/getdata",
        {
          header: {
            language: "zh-Hant-TW",
          },
          requests: [
            {
              action: "getNewsData",
              data: {
                id: this.$route.query.id,
              },
            },
          ],
        },
        { headers: { "Content-Type": "application/json" } }
      )
      .then(function (res) {
        let actionDatas = res.data.responses;
        poxy.item = actionDatas[0].data;
        poxy.isLoading = "visibility:visible;";
      })
      .catch(function (error) {
        console.log(error);
      });
  },
  methods: {
    getDate: function (timestamp) {
      let date = new Date(timestamp * 1000);
      let dataValues = [
        date.getFullYear(),
        date.getMonth() + 1,
        date.getDate(),
        date.getHours(),
        date.getMinutes(),
        date.getSeconds(),
      ];
      if (dataValues[1] < 10) {
        dataValues[1] = "0" + dataValues[1];
      }
      if (dataValues[2] < 10) {
        dataValues[2] = "0" + dataValues[2];
      }
      if (dataValues[3] < 10) {
        dataValues[3] = "0" + dataValues[3];
      }
      if (dataValues[4] < 10) {
        dataValues[4] = "0" + dataValues[4];
      }
      // console.log(
      //   "Date: " +
      //     dataValues[0] +
      //     "/" +
      //     dataValues[1] +
      //     "/" +
      //     dataValues[2] +
      //     " " +
      //     dataValues[3] +
      //     ":" +
      //     dataValues[4] +
      //     ":" +
      //     dataValues[5]
      // );

      return (
        dataValues[1] +
        "/" +
        dataValues[2] +
        " " +
        dataValues[3] +
        ":" +
        dataValues[4]
      );
    },
  },
};
</script>

