import {
  createRouter,
  createWebHashHistory
} from 'vue-router'
import HomeView from '../views/HomeView.vue'
import NewsListView from '../views/NewsListView.vue'
import NewsDetailView from '../views/NewsDetailView.vue'
import FeaturedGamesView from '../views/FeaturedGamesView.vue'
import GameDetailsView from '../views/GameDetailsView.vue'
import FreeGamesView from '../views/FreeGamesView.vue'
import Cover18View from '../views/Cover18View.vue'
import LoginView from '../views/LoginView.vue'
import ContactUsView from '../views/ContactUsView.vue'
import "vue-cookies";

function checkCookies(nextPageView) {
  // console.log(window.$cookies.get("cover18_check_auth"));
  if (!window.$cookies.get("cover18_check_auth")) {
    return Cover18View;
  } else {
    return nextPageView;
  }
}

const routes = [{
    path: '/',
    name: 'Home',
    component: checkCookies(HomeView)
  },
  {
    path: '/Games1',
    name: 'Featured Games',
    component: checkCookies(FeaturedGamesView)
  },
  {
    path: '/Games2',
    name: 'Free Games',
    component: checkCookies(FreeGamesView)
  },
  {
    path: '/GameDetails',
    name: 'Game Details',
    component: checkCookies(GameDetailsView)
  },
  {
    path: '/Contact',
    name: 'Contact Us',
    component: checkCookies(ContactUsView)
  },
  {
    path: '/News',
    name: 'News List',
    component: checkCookies(NewsListView)
  },
  {
    path: '/NewsDetail',
    name: 'News Detail',
    component: checkCookies(NewsDetailView)
  },
  // {
  //   path: '/AdultInfo',
  //   name: 'Adult Info',
  //   component: () => import( /* webpackChunkName: "about" */ '../views/AboutView.vue')
  //   // component: function () {
  //   //   if (window.$cookies.get("cover18_check_auth")) {
  //   //     return Cover18View;
  //   //   } else {
  //   //     return import('../views/AboutView.vue');
  //   //   }
  //   // }
  // },
  {
    path: '/Login',
    name: 'Login',
    component: checkCookies(LoginView)
  },
  {
    path: '/Sign',
    name: 'Sign Up',
    component: checkCookies(LoginView)
  },
  // {
  //   path: '/about',
  //   name: 'about',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  // }
]

const router = createRouter({
  history: createWebHashHistory(),
  scrollBehavior() {
    // always scroll to top
    return {
      top: 0
    }
  },
  routes
})

export default router