<template>
  <main :style="this.isLoading">
    <div class="CarouselWrapper">
      <CarouselWrapper :datas="bannerGames"></CarouselWrapper>
    </div>
    <div class="container popularGames">
      <div class="spanDiv"></div>
      <TextTitle href="/Games1">{{ $t("system.popularGames") }}</TextTitle>
      <div class="spanDiv"></div>
      <div class="spanDiv"></div>
      <div class="spanDiv"></div>
      <div class="row">
        <GameList
          :type="0"
          :direction="0"
          :datas="this.popularGames"
          caption="none"
          :icon="true"
          :crown="true"
        ></GameList>
        <!-- <GameList type="0" direction="1" :datas="demoData['HomeGames6']" caption="none"></GameList> -->
        <!-- <GameList type="1" direction="0" :datas="demoData['HomeGames3']"  caption="left"></GameList> -->
        <!-- <GameList type="1" direction="1" :datas="demoData['HomeGames3']" caption="right"></GameList> -->
      </div>
    </div>
    <div class="container gm_three_part">
      <div class="row">
        <div class="col col-xl-4 col-12">
          <ShadowBox>
            <div class="spanDiv"></div>
            <TextTitle href="/Games1">{{
              $t("system.featuredGames")
            }}</TextTitle>
            <div class="spanDiv"></div>
            <GameList
              :type="1"
              :direction="1"
              :datas="this.featuredGames"
              caption="left"
            ></GameList>
          </ShadowBox>
        </div>
        <div class="col col-xl-4 col-12">
          <ShadowBox>
            <div class="spanDiv"></div>
            <TextTitle href="/Games2">{{ $t("system.freeGames") }}</TextTitle>
            <div class="spanDiv"></div>
            <GameList
              :type="1"
              :direction="1"
              :datas="this.freeGames"
              caption="left"
            ></GameList>
          </ShadowBox>
        </div>
        <div class="col col-xl-4 col-12">
          <ShadowBox>
            <div class="spanDiv"></div>
            <TextTitle href="/Games1">{{
              $t("system.preregisterGames")
            }}</TextTitle>
            <div class="spanDiv"></div>
            <GameList
              :type="1"
              :direction="1"
              :datas="this.featuredGames"
              caption="left"
            ></GameList>
          </ShadowBox>
        </div>
      </div>
    </div>
    <div class="container eventGames">
      <div class="row">
        <div class="spanDiv"></div>
        <TextTitle href="/Games1">{{ $t("system.eventGames") }}</TextTitle>
        <div class="spanDiv"></div>
        <GameList
          :type="1"
          :direction="0"
          :shadow="true"
          :datas="this.featuredGames"
          caption="left"
          :timer="true"
          :icon="true"
        ></GameList>
      </div>
    </div>
    <div class="container newsList_part">
      <div class="row">
        <div class="spanDiv"></div>
        <TextTitle href="/News">{{ $t("system.news") }}</TextTitle>
        <div class="spanDiv"></div>
        <div>
          <ShadowBox>
            <NewsList></NewsList>
          </ShadowBox>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
import axios from "axios";

import CarouselWrapper from "@/components/control/CarouselWrapper.vue";
import GameList from "@/components/control/GameList.vue";
import NewsList from "@/components/NewsList.vue";
import TextTitle from "@/components/control/TextTitle.vue";
import ShadowBox from "@/components/control/ShadowBox.vue";

export default {
  name: "HomeView",
  components: {
    CarouselWrapper,
    GameList,
    NewsList,
    TextTitle,
    ShadowBox,
  },
  data() {
    return {
      isLoading: "visibility:hidden;",
      popularGames: [],
      featuredGames: [],
      freeGames: [],
      bannerGames: [],
    };
  },
  mounted() {
    let poxy = this;
    axios
      .post(
        "https://gaming-dev.murax2.com/api/getdata",
        {
          header: {
            language: "zh-Hant-TW",
          },
          requests: [
            {
              action: "listGames",
              data: {
                length: 6,
                orderBy: "bestseller",
              },
            },
            {
              action: "listGames",
              data: {
                begin: 1,
                length: 3,
                orderBy: "popular",
                order: "desc",
                type: "featured",
              },
            },
            {
              action: "listGames",
              data: {
                begin: 1,
                length: 3,
                orderBy: "date",
                order: "desc",
                type: "free",
              },
            },
            {
              action: "listEvents",
              data: {
                type: "top",
                begin: 1,
                length: 3,
              },
            },
          ],
        },
        { headers: { "Content-Type": "application/json" } }
      )
      .then(function (res) {
        // console.log(res);
        let actionDatas = res.data.responses;
        poxy.popularGames = actionDatas[0].data;
        poxy.featuredGames = actionDatas[1].data;
        poxy.freeGames = actionDatas[2].data;
        poxy.bannerGames = actionDatas[3].data;
        poxy.isLoading = "visibility:visible;";
      })
      .catch(function (error) {
        console.log(error);
      });
  },
};
</script>

